import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',    
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/login',
    name: 'login',    
    component: () => import('../views/login.vue'),
    meta: {requiereLogin:false, title: 'Login'}
  },
  {
    path: '/calendario',
    name: 'Calendario',    
    component: () => import('../views/Calendario.vue'),
    meta: {requiereLogin:true, title: 'Calendario'}
  },
  {
    path: '/justificar',
    name: 'Justificar',    
    component: () => import('../views/Justificar.vue'),
    meta: {requiereLogin:true, title: 'Justificar'}
  },
  {
    path: '/cambio_password',
    name: 'Cambio_Con',    
    component: () => import('../views/Cambio_Contraseña.vue'),
    meta: {requiereLogin:true, title: 'Cambio Contraseña'}

  },
  {
    path: '/administrar_mi_sol',
    name: 'Estatus_Sol',    
    component: () => import('../views/Estatus_De_Mi_Sol.vue'),
    meta: {requiereLogin:true, title: 'Administrar Mi Solicitudes'}
  },  
  {
    path: '/reporte_faltas',
    name: 'Reporte_Faltas',    
    component: () => import('../views/Reporte_De_Faltas.vue'),
    meta: {requiereLogin:true, title: 'Reporte De Faltas'}
  },
  {
    path: '/reporte_faltas_justificadas',
    name: 'Reporte_Faltas_Justificadas',    
    component: () => import('../views/Reporte_Falta_Justificada.vue'),
    meta: {requiereLogin:true, title: 'Reporte Falta Justificada'}
  },
  {
    path: '/administrar_docentes',
    name: 'Administrar_Docentes',    
    component: () => import('../views/Administrar_Docentes.vue'),
    meta: {requiereLogin:true, title: 'Administrar Docentes'}
  },
  {
    path: '/recuperar_contrasenia:token',
    name: 'Recuperar_Con',    
    component: () => import('../views/Recuperar_Contrasenia.vue'),
    meta: {requiereLogin:false, title: 'Recuperar Contraseña'}
  },
  {
    path: '/pruebas/:ID',
    name: 'Pruebas',    
    component: () => import('../views/Pruebas.vue')
  },
  {
    path: '/retardos',
    name: 'retardos',    
    component: () => import('../views/Reporte_Retardos.vue'),
    meta: {requiereLogin:true, title: 'Retardos'}
  }
  ,
  { path: '*', component:() => import('../components/Error404.vue'),  meta: {requiereLogin:false, title: 'ERROR 404'}},


]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {    
    return { x: 0, y: 0 }
  }
})
router.beforeEach((to, from, next) => {  
  document.title = to.meta.title;
  console.log(to.name);
  if(to.name=='Home'){
    next("/login");
  }
  if (to.matched.some(route => route.meta.requiereLogin)) {        
    if (store.state.login) {      
      next()
    }else{      
      next("/login");
    }
  }else{
   if(to.name=="login" && store.state.login){
    next("/calendario");
   }else{
    next()
   }
    
  }
})
export default router
